
.tile             {position: absolute; left: 0; top: 0; width: 25%; height: 25%; -webkit-transition: left .3s ease-in-out, top .3s ease-in-out; transition: left .3s ease-in-out, top .3s ease-in-out; display: flex; justify-content: center; align-items: center; box-sizing: border-box;}

.tile             {border: 1px solid #dee2e6; background-color: var(--light); cursor: pointer; z-index: 2;}
.tile.tile--empty {background-color: var(--white); border: 1px solid transparent; z-index: 1;}
.tile__inner      {font-size: 3rem; font-weight: 300;}

.tiles-wrapper {border: 1px solid var(--dark); padding: 2px; max-width: 500px; width: 100%;}

.tiles         {position: relative; width: 100%; height: 100%;}
.tiles:before  {content: ''; display: block; padding-top: 100%;}

#root      {display: flex; flex-direction: column; min-height: 100vh;}

.app       {flex: 1 1; display: flex; flex-direction: column;}
.container {flex: 1 1; display: flex; flex-direction: column; justify-content: center; align-items: center;}

body {
    margin: 0;
    padding: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.btn .material-icons {
    vertical-align: middle;
}

